import { Component } from '@angular/core';
import {
  IonicPage,
  NavController
} from 'ionic-angular';
import { ProfileProvider } from '../../providers/profile/profile';
import { AuthProvider } from '../../providers/auth/auth';
@IonicPage()
@Component({
  selector: 'page-perfil-cadastro',
  templateUrl: 'perfil-cadastro.html',
})
export class PerfilCadastroPage {
  public userProfile: any;
  public dataNacimento: string;
  constructor(public navCtrl: NavController,  
    public authProvider: AuthProvider,
    public profileProvider: ProfileProvider) {}

  ionViewDidLoad() {
    console.log('ionViewDidLoad PerfilCadastroPage');
  }

  criarPerfil(nome: string, sobrenome:string, dataNascimento: string, rg: string, cpf: string, tel1: string, tel2: string): void {
    this.profileProvider.criarPerfil(nome, sobrenome, dataNascimento, rg, cpf, tel1, tel2)
      .then(newProfile => {
        this.authProvider.logoutUser().then(() => {
          this.navCtrl.setRoot('LoginPage');
        });
      })
  }

}


